@charset "UTF-8";


#container {
    width:100%; background-color:#eee;
    & > section {
        width:100%; height:800px;
    }
    #slideSection {
        height:840px;
        box-sizing:border-box;
        .slide01 {
            width:100%; height:420px; background-color:#f9dd91;
        }
        .slide02 {
            width:100%; height:420px; padding:0 0 0 150px; background-color:#cc6e65;
            @include boxSizing;
            .slide_container {
                width:100%; background-color:#ccc;
                ul {
                    &:after {
                        @extend %clearfix;
                    }
                    li {
                        width:200px; height:200px; background-color:#221567;
                        float:left; margin:0 0 0 20px; cursor:pointer; color:#fff;
                        &:first-child { width:600px; height:400px; }
                    }
                }
            }
        }

    }
    .slide03 {
        background-color:#ccc; height:350px;
    }
}
